import { httpEntranceNoCors } from '../scripts/internal/httpFunc'
/**
 * 
 * @param {Object} sigtestrun 
 */
const initTestRun = sigtestrun => {
  /*
   NOTICE: 
   */
  //  connection
  const runarray = []
  const length = sigtestrun.connection.length
  /**
   * TIPS: ,,N
   */
  for (let index = 0; index < length; index++) {
    sigtestrun.connection.forEach(element => {
      if (runarray.length === 0) {
        // 
        if (element.source.id === 1) {
          // 1
          runarray.push(element) // 
        }
      } else {
        // id
        const destid = runarray[runarray.length - 1].destination.id
        // 
        if (element.source.id === destid) {
          runarray.push(element) // 
        }
      }
    })
  }

  return runarray
}

/**
 * yieldgenerator
 * webtestrun
 * @param {*} sourcedata 
 * @param {*} runsequence 
 */
function* executeWebTestRun(sourcedata, runsequence) {
  //  TIPS: 
  for (let index = 0; index < runsequence.length; index++) {
    const element = runsequence[index]
    if (element.source.id === 1) {
      yield console.log('' + index)
    } else {
      const signode = sourcedata.find(item => item.id === element.source.id)
      if (signode !== undefined) {
        const step = signode.approvers[0].step
        const logic = signode.approvers[0].logic
        // console.log('' + index)
        yield send(step, logic)
      }
    }
  }
}

/**
 * 
 * NOTICE: RequestMain
 * TIPS: TODO:
 */
function send(step, logic) {
  return new Promise(function(resolve, reject) {
    const dateStart = new Date() // 
    httpEntranceNoCors(step)
      .then(function(res) {
        const dateEnd = new Date() // 
        const dateDiff = dateEnd.getTime() - dateStart.getTime() // 
        // 
        const tresponse = {
          headers: res.headers,
          statuscode: res.statusCode,
          statustext: res.statusText,
          body: res.text,
          reqheaders: res.req.header,
          completeres: res,
          usetime: dateDiff
        }
        step.response = tresponse
        resolve({ step: step, logic: logic })
      })
      .catch(function(err) {
        // 
        const dateEnd = new Date() // 
        const dateDiff = dateEnd.getTime() - dateStart.getTime() // 
        const tresponse = {
          headers: undefined,
          statuscode: '500',
          statustext: undefined,
          body: err.toString(),
          reqheaders: undefined,
          completeres: err,
          usetime: dateDiff
        }
        step.response = tresponse
        reject({ step: step, logic: logic })
      })
  })
}

/**
 *  
 * @param {Object} sourceresult 
 */
function resultAssist(sourceresult) {
  // console.log(sourceresult)
  const logicflag = sourceresult.logic.flag
  switch (logicflag) {
    case 0:
      {
        // Code
        const statuscode = sourceresult.step.response.statuscode
        let rresult = false
        if (sourceresult.logic.value === statuscode.toString()) {
          rresult = true
        }
        const result = {
          expand: 'statuscode:' + sourceresult.logic.value,
          actual: 'statuscode:' + statuscode.toString(),
          result: rresult
        }
        sourceresult.result = result
      }
      break
    case 1:
      {
        // response body
        const body = sourceresult.step.response.body
        let rresult = false
        if (body.indexOf(sourceresult.logic.value) !== -1) {
          rresult = true
        }
        const result = {
          expand: 'body include:' + sourceresult.logic.value,
          actual: rresult.toString(),
          result: rresult
        }
        sourceresult.result = result
      }
      break
    case 2:
      {
        const body = sourceresult.step.response.body
        const keyvalue = sourceresult.logic.value.split(':')
        const jsonbody = JSON.parse(body)
        let rresult = false
        containsKey(jsonbody, keyvalue[0])
        if (tempresult !== undefined) {
          if (tempresult[1] === keyvalue[1]) {
            rresult = true
          }
        } else {
          rresult = false
        }
        const result = {
          expand: sourceresult.logic.value,
          actual: tempresult[0] + ':' + tempresult[1],
          result: rresult
        }
        sourceresult.result = result
      }
      break
    default:
      {
        // Code
        const statuscode = sourceresult.step.response.statuscode
        let rresult = false
        if (sourceresult.logic.value === statuscode.toString()) {
          rresult = true
        }
        const result = {
          expand: 'statuscode:' + sourceresult.logic.value,
          actual: 'statuscode:' + statuscode.toString(),
          result: rresult
        }
        sourceresult.result = result
      }
      break
  }
  // console.log(sourceresult)
  return sourceresult
}

/**
 * 
 */
let tempresult = ['', '']
/**
 *  JSONKEY
 * @param {Object} target 
 * @param {String} key Key
 * @returns
 */
function containsKey(target, key) {
  if (typeof target !== 'object') {
    return undefined
  }
  const list = Object.entries(target)
  list.forEach(element => {
    if (element[1] instanceof Object) {
      containsKey(element[1], key)
    } else {
      if (element[0] === key) {
        tempresult = element
      }
    }
  })
}

export { initTestRun, resultAssist }
export { executeWebTestRun }
