<template>
  <section>
    <!-- -->
    <b-field label="" label-position="on-border">
      <b-select v-model="localselectedlogic" placeholder="">
        <option v-for="(option, index) in localchecklogic" :key="index" :value="option">{{ option.name }}</option>
      </b-select>
    </b-field>
    <b-field label="" label-position="on-border">
      <b-input v-model="localselectedlogic.value" placeholder="" lazy></b-input>
    </b-field>
    <b-button expanded size="is-small" @click="closeModal()"></b-button>
  </section>
</template>

<script>
import { CHECKLOGIC } from './../../scripts/internal'
export default {
  name: 'CheckLogic',
  props: {
    selectedlogic: Object
  },
  data() {
    return {
      localchecklogic: [],
      localselectedlogic: {}
    }
  },
  watch: {
    selectedlogic: {
      immediate: true,
      handler(val) {
        this.localselectedlogic = val
      },
      deep: true
    },
    localselectedlogic: {
      immediate: false,
      handler(val) {
        this.$emit('updatelogic', val)
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 
     */
    init() {
      // 
      this.localchecklogic = CHECKLOGIC
    },
    closeModal() {
      this.$emit('close')
    }
  }
}
</script>

<style></style>
